.ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis
}

.container {
  padding: 0 10px;
  width: 100%;
  overflow: hidden
}

.header{
  position: fixed;
  top: 0px;
  left: 0;
  width: 100%;
  z-index: 1000;
}

.header-nav {
  position: fixed;
  top: 51px;
  left: 0;
  width: 100%;
  z-index: 1000;
  border-bottom: 1px solid #e4e4e4
}

.main {
  margin-top: 94px;
}

.rank-head {
  width: 100%;
  height: 43px;
  position: fixed;
  background: -webkit-linear-gradient(top, rgba(0, 0, 0, .6), rgba(0, 0, 0, 0));
  top: 51px;
  z-index: 999;
  color: #fff;
  font-size: 18px;
  text-align: center;
  line-height: 43px;
}

.rank-banner-wrap {
  width: 100%;
  height: 200px;
  margin-top: -43px;
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%
}

.rank-status {
  height: 43px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background: -webkit-linear-gradient(bottom, rgba(0, 0, 0, .6), rgba(0, 0, 0, 0));
  line-height: 43px;
  font-size: 15px;
  color: #fff
}

.rank-info-list .rank-index {
  position: absolute;
  left: 20px;
  font-size: 12px;
  margin-top: 4px
}

.rank-info-list .mint-cell-title {
  padding-left: 35px;
}

.plist .mint-cell-text {
  position: absolute;
  left: 90px;
  top: 23px;
  padding-right: 40px;
  height: 16px;
  overflow: hidden;
  line-height: 16px;
}

.plist .mint-cell-label {
  position: absolute;
  left: 90px;
  top: 38px
}

.plist .mint-cell-label:before {
  content: '';
  display: inline-block;
  width: 12px;
  height: 12px;
  background-size: 100%;
}

.plist-info .mint-cell-title {
  padding-left: 5px
}

.plist-desp {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  position: relative;
  text-align: justify;
}

.plist-desp-p {
  padding-right: 40px;
  line-height: 135%
}

.plist-desp-icon {
  display: block;
  width: 24px;
  height: 24px;
  position: absolute;
  right: 10px;
  top: 10px
}

.plist-desp-hide {
  overflow: hidden;
  height: 1.35em;
}

.cell-group {
  margin: 15px 0;
  width: 100%;
  border: 1px solid #e4e4e4;
  border-radius: 5px;
  background-color: #fbfbfb
}

.cell-item {
  padding: 12px 15px;
  font-size: 15px;
  border-bottom: 1px solid #e4e4e4;
  position: relative
}

.cell-item:after {
  content: '';
  display: block;
  position: absolute;
  width: 12px;
  height: 100%;
  background-size: 12px;
  top: 0;
  right: 15px
}

.cell-item:last-of-type {
  border: none
}

.order-img {
  width: 35px;
}

.order-img p {
  font-size: 13px;
  text-align: center;
  color: #2ba2fa;
  margin-top: 3px
}

/*player*/

.mt-range-thumb {
  width: 16px;
  height: 16px;
  top: 7px
}

.detail_player-time {
  display: block;
  width: 10%;
  height: 30px;
  float: left;
  line-height: 30px;
  color: #fff;
  font-size: 12px;
  text-align: center
}

.detail_player-time:nth-of-type(2) {
  float: right;
  margin-top: -30px;
}

.mt-range {
  width: 80%;
  padding: 0 10px;
  overflow: hidden
}

.mt-range--disabled {
  opacity: 1
}

.mt-range-content {
  margin-right: 7px;
  width: 100%;
}

.player-padding {
  padding: 0 20%
}

.detail_player-control {
  margin-top: 10px;
}

.detail_player-btn {
  display: block;
  width: 33.3%;
  float: left;
  height: 100px;
}

.detail_player-lrc {
  width: 100%;
  height: 60px;
  overflow: hidden;
  margin-bottom: 20px;
  text-align: center;
  color: #fff;
  line-height: 20px;
}

.lrc-content {
  -webkit-transition: all .5s;
  transition: all .5s;
  -webkit-transform: translateZ(0);
          transform: translateZ(0)
}

.lrc-content .disCurrentLrc:last-of-type {
  color: orange;
}

/* override */

.mint-cell {
  padding-left: 10px;
  min-height: 52px
}

.rank .mint-cell-wrapper {
  padding: 10px 5px;
}

.plist .mint-cell-wrapper {
  padding: 10px 5px;
}

.ringtone .mint-cell-wrapper {
  padding: 5px 0 5px 5px
}

.singer_list .mint-cell-wrapper {
  padding: 16px 5px
}

.mint-cell-wrapper {
  background-image: none;
  border-bottom: 1px solid #e8e8e8;
  font-size: 14px;
}

.mint-cell-title {
  padding-right: 10px;
}

/* media query */

@media screen and (max-width: 320px) {
  .detail_player-btn {
    display: block;
    width: 33.3%;
    float: left;
    height: 60px;
  }
}


